.animated {
  animation-duration: 0.5s;
  animation-fill-mode: both;
}

.fadeIn {
  animation-name: fadeIn;
}
.fadeOut {
  animation-name: fadeOut;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.simple-notifier {
  position: fixed;
  top: 1em;
  left: 50%;
  z-index: 10;
  margin-top: -0.75em;
  margin-bottom: -0.75em;
  font-size: 1rem;
  transform: translateX(-50%);
}
@media (min-width: 48em) {
  .simple-notifier {
    top: 2em;
  }
}
@media (min-width: 48em) {
  .simple-notifier--pos-x-left, .simple-notifier--pos-x-right {
    transform: unset;
  }
}
@media (min-width: 48em) {
  .simple-notifier--pos-x-left {
    left: 2em;
  }
}
@media (min-width: 48em) {
  .simple-notifier--pos-x-right {
    left: unset;
    right: 2em;
  }
}
.simple-notifier--pos-y-bottom {
  top: unset;
  bottom: 1em;
}
@media (min-width: 48em) {
  .simple-notifier--pos-y-bottom {
    bottom: 2em;
  }
}

.simple-notification {
  min-width: 17em;
  width: calc(100vw - 2em);
  margin-top: 0.75em;
  margin-bottom: 0.75em;
  padding: 0.75em 1em;
  color: rgba(0, 0, 0, 0.9);
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 0.25em;
  box-shadow: 0 0.125em 0.125em 0 rgba(0, 0, 0, 0.333333);
  backdrop-filter: blur(0.25em);
}
@media (min-width: 30em) {
  .simple-notification {
    width: 24em;
  }
}
@media (min-width: 48em) {
  .simple-notification {
    width: 28em;
  }
}
.simple-notification:not(.is-shown) {
  display: none;
}
.simple-notification--default {
  color: rgba(0, 0, 0, 0.9);
  background-color: rgba(255, 255, 255, 0.9);
}
.simple-notification--success {
  color: rgba(0, 0, 0, 0.9);
  background-color: rgba(209, 231, 221, 0.9);
}
.simple-notification--warning {
  color: rgba(0, 0, 0, 0.9);
  background-color: rgba(255, 243, 205, 0.9);
}
.simple-notification--error {
  color: rgba(255, 255, 255, 0.9);
  background-color: rgba(132, 32, 41, 0.9);
}
.simple-notification__message {
  margin-top: 0;
  margin-bottom: 1em;
}
.simple-notification__message:last-child {
  margin-bottom: 0;
}
